___CSS_LOADER_EXPORT___.push([module.id, `* { box-sizing: border-box; }
body {
  margin: 0;
  font-family: Arial, sans-serif;
  background: #ffffff;
  color: #222;
}

.container {
  max-width: 820px;
  margin: 40px auto;
  padding: 0 16px;
}

.title {
  margin: 0 0 16px;
  font-size: 28px;
}

.card-widget {
  border: 1px solid #ddd;
  border-radius: 10px;
  padding: 16px;
}

.brands {
  display: flex;
  gap: 10px;
  flex-wrap: wrap;
  align-items: center;
  margin-bottom: 14px;
}

.brands img {
  width: 54px;
  height: 34px;
  object-fit: contain;
  border: 1px solid #d9d9d9;
  border-radius: 6px;
  padding: 4px;
  opacity: 0.35;
  filter: grayscale(100%);
  background: #fff;
}

.brands img.active {
  opacity: 1;
  filter: none;
  border-color: #888;
}

.form {
  display: flex;
  gap: 10px;
  align-items: stretch;
  flex-wrap: wrap;
}

.input {
  flex: 1;
  min-width: 240px;
  height: 38px;
  padding: 8px 10px;
  border: 1px solid #cfcfcf;
  border-radius: 6px;
  font-size: 16px;
}

.btn {
  height: 38px;
  padding: 0 16px;
  border: none;
  border-radius: 6px;
  background: #5cb85c;
  color: white;
  cursor: pointer;
  font-size: 15px;
}

.btn:hover { opacity: 0.95; }

.result {
  margin-top: 12px;
  min-height: 22px;
  font-size: 16px;
}

.result.ok { color: #1b7f1b; }
